import React from 'react'
import { Link } from 'gatsby'

let Navbar = (props)=>{
  return (
    <div className="navbar nav-grid">
        <i className="logos material-icons">face</i>
        <ul  className="nav align-left-on-lg">
          <li ><Link to="/" className="bt bt-round-white"> HOME</Link></li>
          <li ><Link to="/Projects" className="bt bt-round-white">PROJECTS </Link></li>
          <li ><a href="https://raw.githubusercontent.com/aayush6194/portfolio/gh-pages/Resume.pdf" className="bt bt-round-white">RESUME</a></li>
        </ul>
      </div>
    );
}

export default Navbar;
